<template>
	
	<yd-layout>
		<yd-navbar slot="navbar" title="公告详情">
			<a @click="goBack" slot="left">
				<yd-navbar-back-icon></yd-navbar-back-icon>
			</a>
		</yd-navbar>
		<scroller class="scroller">
			<div class="top">
				<h3>{{newObject.subject}}</h3>
			</div >
				
			<div class="hearder">
			    <h1>{{newObject.author}} &nbsp; {{newObject.createTime}}&nbsp;</h1>
			    <h5 class="icon yd-icon-watched"></h5>
			    <h2>{{newObject.readTimes}} </h2>
			</div>
			<div class="content">
				<span v-html="newObject.content"></span>   			
			</div>
		</scroller>
    
	
	</yd-layout>

</template>

<script>
import Vue from 'vue';
import router from '@/router/index';
import listItem from '@/components/common/ListItem';
Vue.component(listItem.name, listItem);
export default{
	data(){
		return{
			newObject:this.$route.params.newsItem
		}
	},
		
	methods:{
		addNewsReadTime(){
			var url = _baseUrl + "/mobile/oa/addNewsReadTime.do";
			this.$ajax({
				url: url,
				method: 'POST',
				params:{
					newsId:JSON.stringify(this.newObject.newId)
				}
			}).then(response => {
			}).catch(error => {
				console.log(error);
			});
		}
	},
	created(){
		this.addNewsReadTime();
	}
	
	
	
}
</script>

<style scoped>

.scroller{
	margin-top: 0;
}

 .content >>> *{
	font-family: "微软雅黑" !important;
}

h3,h5{
	margin: 0;
	padding: 0;
}

.clearfix{clear:both}
.list-title{
	font-size: 0.3rem;
}		
.list-price{
	font-size: 0.1rem;
}
.top{
	font-family: "微软雅黑";
	width: 100%;
	margin: 0.1rem auto 0 auto;
}

.top h3{
	font-size: 0.35rem;
	text-align:center;
	color: #333;
}

.top h5{
	float: left;
	margin-right: 0.1rem;
	line-height: 0.7rem;
}


.hearder{
	font-style: italic;
	color: #A9A9A9;
	width: 60%;
	margin: auto;
}

.hearder::after{
	content: '';
	display: block;
	clear: both;
}


.hearder h1,.hearder h2,.hearder h5{
	float: left;
	font-size: 0.24rem;
	color:#a9a9a9;
	line-height: 0.7rem;
	margin: auto;
}

.icon-title{
	color: #04AB02;
	font-size: 0.3rem;
}
.icon-read{
	font-size: 0.15rem;
	float: left;
}
.content{
	background: #fff;
	margin: 0 .12rem;
	padding: .16rem;
	border-radius: .12rem;
}

.content >>> span p{
	text-indent: 2em;
	text-align: justify;
	margin-bottom: 0 !important;
	line-height: .55rem !important;
	color: #666 !important;
	font-size: .3rem;
}

	
</style>


